import React, { Component } from 'react'

import { HashRouter as Router, Route } from 'react-router-dom'

import BookingModalController from './controllers/modal'

class BookingApp extends Component {
  render() {
    return (
      <Router>
        <Route
          path='/booking/:park_id/:id/:selectedDate?'
          component={(({ match }) => <BookingModalController {...match.params} />)}
        />
      </Router>
    )
  }
}

export default BookingApp